.rec-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  transition: all 0.2s ease;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
.rec-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
  transform: translateY(-1px);
}
.rec-card.implemented {
  opacity: 0.7;
  background: #f8fafc;
  border-color: #d1d5db;
}
.rec-card.implemented .card-title {
  text-decoration: line-through;
}
.rec-card .card-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 1rem;
  overflow: hidden;
}
.rec-card .card-header .header-left {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  flex: 1;
  min-width: 0;
}
.rec-card .card-header .header-left .type-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.75rem;
  flex-shrink: 0;
}
.rec-card .card-header .header-left .type-icon.type-cost_optimization {
  background: #dcfce7;
  color: #166534;
}
.rec-card .card-header .header-left .type-icon.type-maintenance {
  background: #dbeafe;
  color: #1e40af;
}
.rec-card .card-header .header-left .type-icon.type-efficiency {
  background: #fef3c7;
  color: #d97706;
}
.rec-card .card-header .header-left .type-icon.type-compliance {
  background: #f3e8ff;
  color: #7c3aed;
}
.rec-card .card-header .header-left .type-icon svg {
  width: 1.25rem;
  height: 1.25rem;
}
.rec-card .card-header .header-left .title-section {
  flex: 1;
  min-width: 0;
}
.rec-card .card-header .header-left .title-section .card-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 0.25rem 0;
  line-height: 1.4;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
}
.rec-card .card-header .header-left .title-section .type-badge {
  display: inline-block;
  padding: 0.25rem 0.5rem;
  background: #f3f4f6;
  color: #6b7280;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
}
.rec-card .card-header .header-right .badges {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  align-items: flex-end;
}
.rec-card .card-header .header-right .badges .priority-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.rec-card .card-header .header-right .badges .priority-badge.priority-low {
  background: #dcfce7;
  color: #166534;
}
.rec-card .card-header .header-right .badges .priority-badge.priority-medium {
  background: #fef3c7;
  color: #d97706;
}
.rec-card .card-header .header-right .badges .priority-badge.priority-high {
  background: #fecaca;
  color: #dc2626;
}
.rec-card .card-header .header-right .badges .impact-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid;
}
.rec-card .card-header .header-right .badges .impact-badge.impact-low {
  background: #f8fafc;
  color: #6b7280;
  border-color: #e2e8f0;
}
.rec-card .card-header .header-right .badges .impact-badge.impact-medium {
  background: #fef3c7;
  color: #d97706;
  border-color: #fbbf24;
}
.rec-card .card-header .header-right .badges .impact-badge.impact-high {
  background: #fecaca;
  color: #dc2626;
  border-color: #f87171;
}
.rec-card .card-header .header-right .badges .confidence-chip {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem 0.5rem;
  background: #e0e7ff;
  color: #4338ca;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
}
.rec-card .card-header .header-right .badges .confidence-chip svg {
  width: 0.75rem;
  height: 0.75rem;
}
.rec-card .card-content {
  margin-bottom: 1.5rem;
  overflow: hidden;
}
.rec-card .card-content .card-description {
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.6;
  margin: 0 0 1rem 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
}
.rec-card .card-content .cost-analysis {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background: #f8fafc;
  border-radius: 0.75rem;
  border: 1px solid #e2e8f0;
  overflow: hidden;
}
.rec-card .card-content .cost-analysis .cost-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: 0;
  overflow: hidden;
}
.rec-card .card-content .cost-analysis .cost-item .cost-label {
  font-size: 0.75rem;
  color: #6b7280;
  font-weight: 500;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
.rec-card .card-content .cost-analysis .cost-item .cost-value {
  font-size: 0.875rem;
  font-weight: 600;
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.rec-card .card-content .cost-analysis .cost-item .cost-value.savings {
  color: #059669;
}
.rec-card .card-content .cost-analysis .cost-item .cost-value.cost {
  color: #dc2626;
}
.rec-card .card-content .timeline {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  padding: 0.75rem;
  background: #f0f9ff;
  border-radius: 0.5rem;
  border: 1px solid #bae6fd;
}
.rec-card .card-content .timeline .timeline-icon {
  color: #0ea5e9;
  flex-shrink: 0;
}
.rec-card .card-content .timeline .timeline-text {
  font-size: 0.875rem;
  color: #0c4a6e;
  font-weight: 500;
}
.rec-card .card-content .actions-list .actions-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.5rem 0;
}
.rec-card .card-content .actions-list .actions-items {
  list-style: none;
  padding: 0;
  margin: 0;
}
.rec-card .card-content .actions-list .actions-items .action-item {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.5;
  margin-bottom: 0.25rem;
}
.rec-card .card-content .actions-list .actions-items .action-item .action-icon {
  color: #8b5cf6;
  flex-shrink: 0;
  margin-top: 0.125rem;
}
.rec-card .card-actions {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}
.rec-card .card-actions .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid transparent;
  text-decoration: none;
}
.rec-card .card-actions .btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}
.rec-card .card-actions .btn.btn-primary {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  border-color: #8b5cf6;
}
.rec-card .card-actions .btn.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
  transform: translateY(-1px);
}
.rec-card .card-actions .btn.btn-primary:active:not(:disabled) {
  transform: translateY(0);
}
.rec-card .card-actions .btn.btn-secondary {
  background: white;
  color: #374151;
  border-color: #d1d5db;
}
.rec-card .card-actions .btn.btn-secondary:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}
.rec-card .card-actions .btn.btn-secondary:active:not(:disabled) {
  transform: translateY(0);
}
.rec-card .card-actions .btn.btn-secondary.implemented {
  background: #dcfce7;
  color: #166534;
  border-color: #bbf7d0;
}
.rec-card .card-actions .btn.btn-secondary.implemented:hover:not(:disabled) {
  background: #bbf7d0;
  border-color: #86efac;
}
.rec-card .card-actions .btn svg {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
}

@media (max-width: 768px) {
  .rec-card {
    padding: 1rem;
  }
  .rec-card .card-header {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }
  .rec-card .card-header .header-left .title-section .card-title {
    font-size: 1rem;
  }
  .rec-card .card-header .header-right .badges {
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .rec-card .card-content .cost-analysis {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .rec-card .card-actions {
    flex-direction: column;
  }
  .rec-card .card-actions .btn {
    justify-content: center;
  }
}
